/* GLOBAL SETTINGS */
:root{
  --green1: #a7ff83;
  --green2: #17b978;
  --green3:  #086972;
  --green4:  #071a52;
  --link-grey: rgb(136, 136, 136);
  --icon-grey: rgb(192, 192, 192);
  --nav-grey: rgb(245, 245, 245);
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

html,
body{
  width: 100%;
  height: 100%;
}

.App{
  /* min-height: 100vh; */
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 100%;
}

a{
  color: var(--link-grey);
  text-decoration: none;
}
a:visited{
  color: var(--link-grey);
  text-decoration: none;
}

ion-icon {
  font-size: 30px;
  color: var(--icon-grey);
  transition: all 0.3s ease-in-out;
}

ion-icon:hover {
  color: #17b978;
  transform: scale(1.15);
  transform-origin: center;
}



/* BASE STYLES */
.container{
  margin: 0 5%;
}



/* NAVIGATION */
nav{
  /* background-color: var(--nav-grey); */
}

.navbar{
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}
.nav-left{
  display: flex;
  justify-self: start;
  align-items: center;
}

.brand{
  font-size: 1.5rem;
  font-weight: bold;
  padding:0 1rem;
  transition: all 0.5s ease-in-out;
}
.brand:hover {
  color: var(--green2)
}

/* right navigation */
.nav-right{
  position: absolute;
  text-align: right;
  top: 100%;
  right: 0;
  transform: scale(1, 0);
  transition: transform 300ms ease-in-out;
  transform-origin: top;
}
.nav-right ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav-right li{
  margin-bottom: 1em;
  margin-right: 1em;
}
.nav-right a{
  font-size: 1.2rem;
  font-weight: light;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}
.nav-right label{
  font-size: 1.2rem;
  font-weight: light;
  color: var(--link-grey);
  cursor: pointer;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}
.nav-right a:hover{
  color: var(--green2)
}
.nav-right label:hover{
  color: var(--green2)
}
.nav-toggle{
  display: none;
}
.nav-toggle:checked ~ .nav-right{
  transform: scale(1,1);
}
.nav-toggle:checked ~ .nav-right a{
  opacity: 1;
  transition: opacity 200ms ease-in-out 200ms;
}
.nav-toggle:checked ~ .nav-right label{
  opacity: 1;
  transition: opacity 200ms ease-in-out 200ms;
}


/* PAGE STRUCTURE */
.main{
}

.project-controls{
  display: none;
}
.project-content{
  display: flex;
  flex-direction: column;
  margin: 0 1rem;
}

.project-image{
  /* background-color: rgb(210, 210, 255); */
}
.project-card{
  /* background-color: rgb(211, 240, 157); */
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}

.project-info h2{
  padding-top: 1rem;
  padding-bottom: 0.5rem;
}
.project-info h5{
  padding-bottom: 0.5rem;
}
.technologies{
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
}
.technologies ul{
  list-style: none;
}
.technologies li{
  display: inline-block;
  padding-left: 1rem;
}




/* FOOTER */
footer{
  background-color: var(--nav-grey);
}
.footer{
  display: flex;
  justify-content: center;
  padding: 1rem 0;
}
.footer .nav-item{
  padding: 0 0.5rem;
}



/* MEDIA QUERIES */
@media screen and (min-width: 800px){
  .App{
    min-height: 100vh;
  }
  
  .container{
    margin: 0 15%;
  }
  .nav-toggle-label{
    display: none;
  }
  .nav-right{
    all: unset;
    /* text-align: right; */
  }
  .nav-right ul{
    display: flex; 
  }
  .nav-right li{
    margin: 0;
    padding: 0;
    padding-left: 1.5rem;
    list-style: none;
    cursor: pointer;
  }
  .nav-right a{
    opacity: 1;
    font-size: 1.2rem;
    font-weight: light;
    color: var(--link-grey);
  }
  .nav-right label{
    font-size: 1.2rem;
    font-weight: light;
    color: var(--link-grey);
    cursor: pointer;
    opacity: 1;
    transition: opacity 150ms ease-in-out;
  }
  .nav-right label:hover{
    color: var(--green2)
  }
  /* project menu toggle */
  .nav-project{
    right: 00%;
    top: 2.7rem;
    transform: scale(1, 0);  
    transition: transform 300ms ease-in-out;
    transform-origin: top;
  }
  .nav-project ul{
    display: block;
    text-align: right;
  }
  .nav-project li{
    color: var(--link-grey);
    opacity: 0;
    transition: opacity 200ms ease-in-out;
    cursor: pointer;
    margin-top: 0.6rem;
  }
  .nav-project li:hover{
    color: var(--green2)
  }
  .nav-project-toggle{
    display:none;
  }
  .nav-project-toggle:checked ~ .nav-project{
    transform: scale(1,1);
  }
  .nav-project-toggle:checked ~ .nav-project li{
    opacity: 1;
    transition: opacity 200ms ease-in-out 100ms;
  }

  /* display controls for each project */
  #p1{
    display: flex;
  }
  #p2{
    display: none;
  }
  #p3{
    display: none;
  }
  #p4{
    display: none;
  }

  .toggle-title{
    padding: 0.1rem 2rem;
  }
  .project-controls{
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .project-card{
    flex-direction: row;
  }
  .project-info{
    margin-left: 1rem
  }
  .project-info h2{
    padding-top: 1rem;
    padding-bottom: 0.5rem;
  }
  .project-info h5{
    padding-bottom: 0.5rem;
  }
}



